<template>
  <div id="product">
    <section style="display: flex">
      <ElInput
        v-model="search"
        style="width: 500px"
        placeholder="临时功能:输入商品名称进行搜索后,会优先显示"></ElInput>
      <ElButton @click="filterTicket">确认搜索</ElButton>
    </section>
    <div class="tableHeader">
      <p>创建日期</p>
      <p>用户</p>
      <p>身份</p>
      <p>版本</p>
      <p>申请理由</p>
      <p>状态</p>
      <p>审核</p>
    </div>
    <div class="divideLine"></div>
    <div class="emptyDiv"></div>
    <Scroll class="content2" :probe-type="3" ref="scroll">
      <order-list :list="listShow"></order-list>
      <div class="paginationZone" v-show="listShow.length !== 0">
        <a-pagination
          :total="listAll.length"
          :show-total="(total, range) => `${range[0]}-${range[1]} of ${total} items`"
          :page-size="pageSize"
          :default-current="1"
          v-model:current="currentPage" />
      </div>

      <empty-data-placeholder
        v-if="listShow.length === 0"
        description="暂无工单数据"></empty-data-placeholder>
    </Scroll>

    <div class="right-absolute-flex" v-show="isShowRightCoverZone">
      <review-comment-dialog
        @cancel="cancelClick"
        @ensure="ensureClick"
        ref="inputReasonProduct"></review-comment-dialog>
    </div>

    <div class="fixedCoverPage" v-if="isShowCoverPage"></div>

    <!-- 工单详情查看弹框 -->
    <div class="absoluteScrollDialogStore">
      <div
        class="itemcontent"
        style="
          background-color: #000;
          width: 0px;
          height: 0px;
          margin-left: 10px;
          margin-bottom: 3px;
        ">
        <div
          class="details"
          v-show="viewBoardVisible"
          style="
            width: 0px;
            height: 0px;
            background-color: #fff;
            box-shadow: var(--color-text-grey) 0 0 7px;
            overflow: hidden;
            position: absolute;
            z-index: 9;
          ">
          <p class="imageZone" @click="hideViewDialog()" v-show="ifShowCancelIcon"></p>
          <Scroll ref="roll" class="content3">
            <product-ticket-content :ticketContent="activeViewItem"></product-ticket-content>
          </Scroll>
        </div>
      </div>
    </div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>
  </div>
</template>

<script>
import Scroll from '../../../components/common/scroll/Scroll.vue'
import { $getAllTicketAudit, $getFilterTicketAudit } from '../../../network/auditSystem/common'
import { $confirmAuditTicket, $declineAuditTicket } from '../../../network/auditSystem/common'
import OrderList from '../../../components/content/auditSystem/orderList/OrderList.vue'
import $ from 'jquery'
import { UrlDecode } from '../../../common/utils'
import ReviewCommentDialog from '../../../components/content/auditSystem/reviewCommentDialog/ReviewCommentDialog.vue'
import EmptyDataPlaceholder from '../../../components/common/emptyDataPlaceholder/EmptyDataPlaceholder.vue'
import ProductTicketContent from './childComponents/productTicketContent.vue'

export default {
  name: 'e-Provider',
  /**
   * 组件内路由守卫
   */
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem('isStaff') === 'true') {
      window.localStorage.setItem('currentIndex010003', 5)
      next()
    } else {
      next({
        path: '/tools/login'
      })
      window.localStorage.removeItem('list')
      window.localStorage.removeItem('order')
      window.localStorage.removeItem('currentIndex010003')
      window.localStorage.removeItem('userID')
      window.localStorage.removeItem('username')
      window.localStorage.removeItem('authToken')
      window.localStorage.removeItem('isStaff')
      window.localStorage.removeItem('isSuperUser')
      window.localStorage.removeItem('isProvider')
      window.localStorage.removeItem('providerID')
    }
  },
  /**
   * 每一次组件处于活跃状态时
   */
  async mounted() {
    let data = {
      work_line_id: 1400
      //"work_operation": 4300,
      //  "status": 3200,
      // "start_date": "2021-12",
      // "end_date": "2022-02"
    }
    this.isLoading = true
    try {
      const response = await $getFilterTicketAudit(data)
      console.log('filter', response)
      const { Code, msg } = response
      // debugger
      // 获取当前登录用户所有工单数据
      if (Code === 200 && msg === 'success') {
        console.log(response.details.tickets)
        this.listAll = response.details.tickets.reverse() // 将数据赋值给listAll
        this.listAll.sort((a, b) => b.id - a.id)
        this.totalPage = Math.ceil(this.listAll.length / this.pageSize) // 重新计算totalPage
        const tempListAll = window.JSON.parse(window.JSON.stringify(this.listAll))
        // 若listShow数组为空，则说明是首次加载组件
        if (this.listShow.length === 0) {
          this.listShow = tempListAll.slice(0, this.pageSize)
        }
        // 若进来页面时分页的currentPage和分页的totalPage相等，因为listAll的数据有可能进行了更新，所以需要更新listShow
        if (this.currentPage === this.totalPage) {
          this.listShow = tempListAll.slice(
            this.pageSize * (this.currentPage - 1),
            this.pageSize * this.currentPage
          )
        }
        this.backupList = this.listAll
      } else {
        this.$toast.error('获取工单数据失败 ', 240)
        console.log('获取工单数据失败 ')
      }
    } catch (err) {
      this.$toast.error('获取工单数据失败  ', 240)
      console.log('获取工单数据失败 ')
    }
    this.isLoading = false

    // 监听到此事件后，做打开工单详情查看弹框的操作
    this.$bus.$on('viewDetailsClick', this.viewDetailsClick)
    this.$bus.$on('passClick', this.passClick)
    this.$bus.$on('passProduct', this.passProduct)
    this.$bus.$on('rejectClick', this.rejectClick)
  },
  /**
   * 当组件处于非活跃状态时
   */
  unmounted() {
    this.$bus.$off('viewDetailsClick', this.viewDetailsClick)
    this.$bus.$off('passClick', this.passClick)
    this.$bus.$off('passProduct', this.passProduct)
    this.$bus.$off('rejectClick', this.rejectClick)
  },
  data() {
    return {
      listAll: [],
      listShow: [],
      pageSize: 8,
      currentPage: 1,
      totalPage: 0,
      isShowCoverPage: false,
      viewBoardVisible: false,
      x: null,
      y: null,
      activeViewItem: [],
      isShowRightCoverZone: false,
      activeRecordIndex: -1,
      search: '',
      isLoading: false,
      backupList: [],
      ifShowCancelIcon: false
    }
  },
  methods: {
    passClick(index) {
      console.log('product passClick index: ', index)

      this.activeRecordIndex = index

      this.$refs.inputReasonProduct.word = '通过'
      this.$refs.inputReasonProduct.message = ''
      this.isShowRightCoverZone = true
      this.isShowCoverPage = true
    },
    filterTicket() {
      this.listShow = this.backupList.filter(l => l?.content.full_name?.includes(this.search))
    },
    passProduct(itemId) {
      console.log('product passClick item id : ', itemId)
      this.activeRecordId = itemId
    },
    viewDetailsClick(param) {
      console.log('click')
      const { x, y, index } = param

      this.x = x
      this.y = y
      const elex = this.x
      const eley = this.y
      // this.activeViewItem = this.simpleProcessPresentInfo(this.listShow[index].content);
      this.activeViewItem = this.listShow[index].content
      console.log(this.activeViewItem)

      const vm = this
      const str = '.details'
      this.isShowCoverPage = true
      this.ifShowCancelIcon = true
      // 计算第二个动画要移动到哪个点
      let targetLeft
      const clientWidth = document.body.clientWidth
      if (clientWidth < 1000) {
        targetLeft = 0
      } else if (clientWidth > 1000 && clientWidth < 1240) {
        targetLeft = (clientWidth - 1000) / 2
      } else {
        targetLeft = 240 + (clientWidth - 240 - 1000) / 2
      }

      let targetTop
      let clientHeight = document.body.clientHeight
      if (clientHeight < 680) {
        targetTop = 0
      } else {
        targetTop = (clientHeight - 680) / 2
      }

      $(str).animate({ left: elex, top: eley }, 50, function () {
        vm.viewBoardVisible = true
      })
      $(str).animate(
        { left: targetLeft, top: targetTop, width: '100px', height: '100px' },
        100,
        function () {
          vm.$refs.roll.refresh()
          vm.$refs.roll.scrollTo(0, 0, 0)
        }
      )
      $(str).animate({ width: '1000px', height: '680px' }, 150)
    },
    rejectClick(index) {
      // console.log(index);
      this.activeRecordIndex = index
      this.$refs.inputReasonProduct.word = '拒绝'
      this.$refs.inputReasonProduct.message = ''
      this.isShowRightCoverZone = true
      this.isShowCoverPage = true
    },
    VMUrlDecode(str) {
      return UrlDecode(str)
    },
    hideViewDialog() {
      this.activeViewItem = {}
      this.ifShowCancelIcon = false

      const str = '.details'
      const vm = this
      $(str).animate({ width: '70px', height: '70px' }, 50)
      $(str).animate({ left: vm.x, top: vm.y, width: '0px', height: '0px' }, 200, function () {
        vm.isShowCoverPage = false
        vm.viewBoardVisible = false
      })
    },
    async ensureClick(message) {
      if (message === '') {
        this.$toast.warn('编辑理由不能为啊空', 240)
        return
      }

      this.$refs.inputReasonProduct.ifShowLoadingGif = true
      console.log(this.listAll[this.activeRecordIndex].id)
      console.log('list all: ')
      console.log(this.listAll)
      console.log('list all index: ')
      console.log(this.activeRecordIndex)
      console.log('record id newly added: ')
      console.log(this.activeRecordId)

      if (this.$refs.inputReasonProduct.word === '通过') {
        try {
          const response = await $confirmAuditTicket({
            id: this.activeRecordId,
            review_comment: message
          })
          console.log('product pass ensure res: ', response)
          // this.$toast.success('操作成功', 240);
          // console.log(this.activeRecordIndex);
          // this.$set(this.listShow[this.indexInListShow], 'status', response.details.status);
          // this.$set(this.listShow[this.indexInListShow], 'review_comment', response.details.review_comment);
          // this.$set(this.listAll[this.activeRecordIndex], 'status', response.details.status);
          // this.$set(this.listAll[this.activeRecordIndex], 'review_comment', response.details.review_comment);
          //
          // this.indexInListShow = -1;

          if (response.Code == 200) {
            this.$toast.success('操作成功', 240)

            this.$router.go(0)
          } else {
            this.$toast.error('操作失败', 240)
            console.log(response)
            console.log(response.Msg)
            this.$toast.error(response.Msg, 240)
          }
        } catch (err) {
          this.$toast.error('操作失败', 240)
          this.indexInListShow = -1
        }

        this.$refs.inputReasonProduct.ifShowLoadingGif = false
        this.isShowCoverPage = false
        this.isShowRightCoverZone = false
      }

      if (this.$refs.inputReasonProduct.word === '拒绝') {
        try {
          const response = await $declineAuditTicket({
            // id: this.listAll[this.activeRecordIndex].id,
            id: this.activeRecordId,
            review_comment: message
          })
          // console.log(response);
          // this.$toast.success('操作成功', 240);
          // console.log(this.activeRecordIndex);
          // this.$set(this.listShow[this.indexInListShow], 'status', response.details.status);
          // this.$set(this.listShow[this.indexInListShow], 'review_comment', response.details.review_comment);
          // this.$set(this.listAll[this.activeRecordIndex], 'status', response.details.status);
          // this.$set(this.listAll[this.activeRecordIndex], 'review_comment', response.details.review_comment);
          //
          // this.indexInListShow = -1;
          if (response.Code == 200) {
            this.$toast.success('操作成功', 240)
            this.$router.go(0)
          } else {
            this.$toast.error('操作失败', 240)
            console.log(response.Msg)
            this.$toast.error(response.Msg, 240)
          }
        } catch (err) {
          this.$toast.error('操作失败', 240)
          this.indexInListShow = -1
        }

        this.$refs.inputReasonProduct.ifShowLoadingGif = false
        this.isShowCoverPage = false
        this.isShowRightCoverZone = false
      }
    },
    cancelClick() {
      this.isShowCoverPage = false
      this.isShowRightCoverZone = false
    },
    simpleProcessPresentInfo(obj) {
      const arr = Object.entries(obj)
      return arr
    }
  },
  watch: {
    currentPage(newVal) {
      const tempListAll = window.JSON.parse(window.JSON.stringify(this.listAll))
      this.listShow = tempListAll.slice(this.pageSize * (newVal - 1), this.pageSize * newVal)
      this.$refs.scroll.scrollTo(0, 0, 400)
      setTimeout(() => {
        this.$refs.scroll.refresh()
      }, 200)
    }
  },
  components: {
    Scroll,
    OrderList,
    ReviewCommentDialog,
    EmptyDataPlaceholder,
    ProductTicketContent
  }
}
</script>

